.footer-container {
    background-color: var(--secondary);
}
.footer {
    padding: 7.5rem 0;
    display: flex;
    align-items: flex-start;
    flex-direction: column;

    .section-title {
        margin-bottom: 1.5rem;
    }

    @media only screen and(min-width:$v-7) {
        justify-content: flex-start;
        flex-direction: row;
        flex-wrap: wrap;
    }
    .footer-heading {
        --spacer: 2rem;

        flex: 1.5;
    }
    & > * {
        min-width: 25rem;
        flex: 1;
        flex-shrink: 0;
        padding: 2.5rem 0;
    }
}

.footer-menu {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    &-home,
    &-about,
    &-benefits,
    &-services {
        width: auto;
        line-height: 1.8;
        font-weight: 600;
    }
}
.footer-contact {
    &-email,
    &-phone {
        line-height: 2;
        display: flex;
        align-items: center;
        img {
            margin-right: 1.5rem;
            width: 2.5rem;
            height: 2.5rem;
        }
    }
}
.footer-social {
    &-icons {
        display: flex;
        justify-content: space-between;
        max-width: 25rem;
        img {
            width: 2.5rem;
            height: 2.5rem;
            flex-shrink: 0;
        }
    }
}
